

.review_list {
  margin: 30px 0;
}

.textfild {
  width: 80%;
  height: 100px;
  max-width: 800px;
  display: flex;
  margin-bottom: 30px;

  > div {
    display: flex;
    flex-direction: column;
    width: 85%;
    padding: 0 10px;
    border: 1px solid #ddd;
    border-radius: 10px 0 0 10px;

    > .text_title {
      width: 100%;
      border: 0;
      border-bottom: 1px solid #ddd;
      height: 30%;
    }

    > .text_body {
      width: 100%;
      height: 70%;
      border: 0;
      padding: 10px 0;
      resize: none;
    }

    .text_body:focus,
    .text_title:focus {
      outline: 0;
    }
  }

  > button {
    width: 15%;
    min-width: 40px;
    border: 0;
    background-color: #bbb;
    color: #fff;
    font-weight: bold;
    border-radius: 0 10px 10px 0;
    cursor: pointer;

    &:hover {
      opacity: 0.8;
    }
  }
}

.postsList {
  width: 100%;
  display: flex;
  flex-direction: column;

  > .post {
    position: relative;
    width: 80%;
    max-width: 800px;
    padding: 20px;
    margin: 10px auto;
    border: 1px solid #ddd;
    border-radius: 15px;

    .closeBtn {
      width: 30px;
      height: 30px;
      position: absolute;
      right: 15px;
      top: 15px;
      cursor: pointer;

      &::before,
      &::after {
        position: absolute;
        right: 14px;
        content: "";
        height: 30px;
        width: 2px;
        background-color: #ddd;
      }

      &::before {
        transform: rotate(45deg);
      }

      &::after {
        transform: rotate(-45deg);
      }
    }

    > .post_date {
      padding: 10px 0;
      font-size: 0.7em;
      color: rgb(90, 90, 90);

      span {
        margin-right: 10px;
        &:first-child::after {
          content: "|";
          display: inline-block;
          margin-left: 10px;
        }
      }
    }

    > .post_title {
      font-size: 1.1em;
      font-weight: bold;
    }

    > .post_text {
      margin-top: 5px;
      font-size: 0.9em;
    }
  }
}
