@import "../../common.scss";

.login_page {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;

  form {
    display: flex;
    align-items: center;
    position: relative;
    flex-direction: column;
    margin: auto;
    width: 40%;
    max-width: 600px;
    min-width: 400px;
    height: 40%;
    max-height: 450px;
    min-height: 350px;
    border: 1px solid #bbb;
    border-radius: 30px;
    overflow: hidden;
  }

  .login_title {
    font-size: 2em;
    padding: 10% 0 5%;
  }

  .id {
    border: 1px solid #ddd;
    border-radius: 5px 5px 0 0;
    path {
      fill: #666;
    }
  }
  .pw {
    border: 1px solid #ddd;
    border-top: 0;
    margin-bottom: 30px;
    border-radius: 0 0 5px 5px;
    path {
      fill: #ddd;
    }
  }

  .id,
  .pw {
    position: relative;
    width: 70%;
    padding: 10px 20px;

    label {
      display: inline-block;
      height: 20px;
      width: 20px;

      svg {
        height: 100%;
        width: auto;
      }
    }

    input {
      position: relative;
      top: -5px;
      outline: none;
      width: calc(100% - 60px);
      height: 20px;
      margin-left: 10px;
      border: 0;
    }

    .login_delete {
      position: relative;
      top: 3px;
      width: 30px;
      height: auto;
      cursor: pointer;

      path {
        fill: #ddd;
      }
    }
  }

  .search_my {
    display: flex;
    width: 280px;
    justify-content: space-between;
    font-size: 0.8em;
    color: var(--gray-color);

    li {
      position: relative;
      cursor: pointer;

      &::after {
        content: "|";
        position: absolute;
        right: -20px;
        color: #ddd;
        cursor: auto;
      }
      &:last-child::after {
        content: "";
      }
    }
  }

  .submit_btn {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 50px;
    border: 0;
    border-top: 1px solid #bbb;
    cursor: pointer;

    &:hover {
      background-color: #ddd;
    }
  }
}
