.shop {
  display: flex;
  justify-content: center;

  .menuSection {
    width: 100%;
    max-width: 1200px;
    
    h2 {
      padding: 50px 0 30px;
      text-align: center;
      margin-bottom: 20px;
    }
    
    .optionSection {
      // position: relative;
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding-bottom: 20px;
      border-bottom: 1px solid #ddd;

      .searchWrap {
        .search {
          width: 200px;
          padding: 5px;
          border: 0;
          border-bottom: 1px solid #666;
        }
      }
    }

    .priceOption {
      // position: absolute;
      // top: -30px;
      // right: 0;
      padding: 5px 10px;
      border: 1px solid #ddd;
      color: #666;
    }
  }


  .menuBox {
    width: 100%;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;


    li {
      margin: 25px auto;
    }

    img {
      width: 200px;
    }

    p {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      text-align: center;
      width: 200px;
      &.title {
        margin: 10px 0 5px;
        font-size: 1.2em;
      }
    }
  }
}