

:root {
  --logo-width: 200px;
  --btn-small-padding: 5px 10px;
}

nav {
  width: 100vw;
  background-color: #fff;
  border-bottom: 1px solid #ddd;

  > div {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    height: 100px;
    color: var(--gray-color);
    background-color: #fff;
    //   @extend %exborder;

    .main_logo {
      display: flex;
      width: var(--logo-width);
      height: auto;
      margin: 0;
    }

    .hidden {
      display: none;
    }

    > .menu {
      display: flex;
      width: calc(100% - var(--logo-width));
      padding-left: 3%;
      justify-content: space-between;

      > .category {
        display: flex;
        margin: 0;
        > li {
          word-break: keep-all;
          padding: var(--btn-small-padding);
          margin-right: 10px;
          font-size: 1.2em;
          font-weight: 700;
          &:hover {
            cursor: pointer;
            color: var(--point-color);
          }
        }
      }
      > .login_menu {
        display: flex;
        // width: 40%;
        margin: 0;

        > li {
          word-break: keep-all;
          margin-left: 5px;
          padding: var(--btn-small-padding);
          &:hover:not(.welcome) {
            cursor: pointer;
            color: #000;
          }

          &.welcome {
            color: #bbb;
          }

        }
      }
    }
  }
}
