@mixin linear-gra-black($to) {
  content: '';
  position: absolute;
  top: 0;
  display: block;
  width: 10%;
  height: 100%;
  background: linear-gradient(
    $to,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 0, 0, 0.5) 40%,
    rgba(0, 0, 0, 0) 100%
  );
}

.main_loading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  span {
    margin-top: 20px;
    color: var(--gray-color);
  }
}
