.detail_page {
  width: 100vw;
}

.movie_detail {
  width: 100%;
}

.top_info {
  width: 100%;
  max-width: 1200px;
  display: flex;
  padding: 50px 100px;

  .top_image {
    width: 200px;
    img {
      width: 100%;
      height: auto;
    }
  }
  .top_text {
    position: relative;
    width: calc(100% - 180px - 50px);
    margin-left: 50px;
    .top_title {
      font-size: 2rem;
      font-weight: 800;
    }
    .top_like {
      margin-top: 30px;
      font-size: 1.2rem;
      span {
        margin-right: 15px;
        svg {
          margin-right: 5px;
        }
      }
    }
    .top_genres {
      width: 100%;
      line-height: 30px;

      span,
      li {
        float: left;
        margin-right: 5px;
      }
      li::after {
        content: ", ";
        display: inline-block;
      }
      li:last-child::after {
        content: "";
      }

      &::after {
        content: "";
        display: block;
        width: 100%;
        height: 30px;
      }
    }

    .top_runtime {
      padding-bottom: 30px;
      border-bottom: 1px solid #eee;
    }

    .top_btn {
      position: absolute;
      bottom: 5px;
      left: 0;

      p {
        display: inline-block;
        width: 160px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        background-color: var(--point-color);
        margin-right: 10px;
        color: #fff;
        border-radius: 10px;
        cursor: pointer;
      }
    }
  }
}

.bottom_info {
  width: 100%;
  background-color: #eee;

  .info_des {
    max-width: 1200px;
    padding: 50px 100px;
    font-size: 1.2rem;
    text-align: justify;
  }
}

.youtube {
  width: 100%;
  max-width: 1200px;
  padding: 50px 0px;

  p {
    text-align: center;
    font-size: 2rem;
  }

  div {
    margin-top: 30px;
    display: flex;

    ul {
      flex-grow: 1;
      color: var(--gray-color);
      .thum {
        width: 100%;
        height: 200px;
        background-color: #ddd;
      }
      .title {
        margin-top: 15px;
      }
      .date {
        margin-top: 5px;
        font-size: 0.8rem;
      }

      &:nth-child(2) {
        margin: 0 15px;
      }
    }
  }
}

.review {
  width: 100%;
  max-width: 1200px;
  padding: 50px 0px;

  p {
    text-align: center;
    font-size: 2rem;
  }
}
